@use "button" as *;
@use "mixins" as *;
@use "icon" as *;
@use "font" as *;
@use "container" as *;

.chat {
  flex-grow: 1;
  display: grid;

  &__container {
    @extend %container-fluid;
  }

  &__inner {
    display: grid;
    grid-template-columns: minmax(min-content, 320px) 1fr;
    height: 100%;
  }

  &__aside {
    border-right: var(--default-border);
    padding: gap-scale(1) gap-scale(0.5) gap-scale(1) 0;
    display: flex;
    flex-direction: column;
    gap: 24px;
    height: 0;
    min-height: 100%;
    overflow: auto
  }

  &__create {
    @extend %button;
    max-width: unset;
    width: 100%;

    &::after {
      content: "";
      @extend %icon, %icon--add;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: var(--list-gap);
    --list-gap: 16px;

    &__group {
      display: flex;
      flex-direction: column;
      gap: 4px;

      &:not(:first-child) {
        padding-top: var(--list-gap);
        border-top: var(--default-border-width) dashed var(--default-border-color);
      }

      &__title {
        @extend %font-4;
      }
    }

    &__item {
      display: flex;
      align-items: center;
      padding: 0 8px;
      gap: 8px;

      &__title {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 0;
        flex-grow: 1;
        padding: 6px 0;
        @include hoverType2;
      }

      &:not(:hover) &__menu:not(.is-active) {
        width: 0;
        overflow: hidden;
      }

      &__menu {
        display: flex;
        position: relative;

        &__button {
          border-radius: var(--default-border-radius-small);

          &:hover,
          &.is-active {
            background-color: var(--color-primary);
            color: var(--color-text-alt);
          }

          &::after {
            content: "";
            @extend %icon, %icon--more_h;
          }
        }

        &__item {
          color: var(--color-primary);
          background-color: #0000;
          border-radius: var(--default-border-radius-small);
          padding: 4px;

          &--danger {
            color: var(--color-accent-alt1);
          }

          &:hover {
            background-color: color-mix(in srgb, currentColor 10%, #0000);
          }
        }

        &:not(.is-active) &__popover {
          display: none;
        }

        &__popover {
          display: flex;
          gap: 4px;
          position: absolute;
          bottom: calc(100% + 8px);
          right: 0;
          box-shadow: 0 0 8px 0 rgba(34, 60, 80, 0.2);
          width: max-content;
          background: var(--color-text-alt);
          padding: 4px;
          border-radius: var(--default-border-radius-small);
        }
      }
    }
  }

  &__main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 24px;
    padding: gap-scale(1);
  }

  &__welcome {
    text-align: center;
    @extend %h2;
  }

  &__form {
    display: flex;
    flex-direction: column;
    gap: 16px;

    &__controls {
      display: flex;
      justify-content: space-between;
      gap: 24px;
    }

    .select-simple {
      &__select {
        height: 100%;
      }

      select {
        height: 100%;
      }
    }

    .input {
      textarea {
        resize: none;
      }
    }
  }

  &__title {
    @extend %h2;
    text-align: center;
  }

  &__action {
    @include hoverType2;
  }

  &__dialog {
    display: flex;
    flex-direction: column;
    gap: var(--dialog-gap);
    flex-grow: 1;
    height: 0;
    overflow: auto;
    position: relative;
    --dialog-gap: gap-scale(1);

    &__message {
      max-width: min(90%, 700px);
      align-self: flex-end;
      display: flex;
      flex-direction: column;
      gap: 8px;

      &__text {
        padding: gap-scale(0.5);
        background-color: color-mix(in srgb, var(--color-primary) 10%, #0000);
        border-radius: var(--default-border-radius);
      }

      &:not(:hover) &__actions {
        opacity: 0;
      }

      &__actions {
        display: flex;
        justify-content: flex-end;
        gap: 12px;
        transition: opacity ease var(--default-duration);
      }
    }

    &__response {
      display: flex;
      flex-direction: column;
      gap: 8px;

      &:not(:hover) &__actions {
        opacity: 0;
      }

      &__actions {
        display: flex;
        gap: 12px;
        transition: opacity ease var(--default-duration);
      }
    }

    &__goto {
      margin-top: calc(var(--dialog-gap) * -1);
      position: sticky;
      bottom: gap-scale(1);
      padding: 0 gap-scale(1);
      height: 0;
      display: flex;
      align-items: flex-end;
      justify-content: flex-end;

      &:not(&--down):not(&--up) {
        display: none;
      }

      &:not(&--down) &__button--down {
        display: none;
      }

      &:not(&--up) &__button--up {
        display: none;
      }

      &__button {
        display: flex;
        padding: 8px;
        background-color: var(--color-text-alt);
        box-shadow: 0 0 8px 0 rgba(34, 60, 80, 0.2);
        border-radius: 50%;

        &::after {
          content: "";
          @extend %icon;
        }

        &--up {
          @include hoverType2;

          &::after {
            @extend %icon--arrow_upward;
          }
        }

        &--down {
          @include hoverType2;

          &::after {
            @extend %icon--arrow_downward;
          }
        }
      }
    }
  }
}